@font-face {
  font-family: 'Pretendard';
  src: url('/assets/fonts/Pretendard-Thin.woff2') format('woff2'),
      url('/assets/fonts/Pretendard-Thin.woff') format('woff'),
      url('/assets/fonts/Pretendard-Thin.ttf') format('truetype');
